<template>
  <div>
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-1">
        <b-avatar class="mr-1" size="40" variant="light-success">
          <book-icon :style="{ color: 'var(--success)' }" size="30"/>
        </b-avatar>
        <h4 class="m-0">ثبت فرمول شارژ {{ chargeName }}</h4>
      </div>
      <span>
        {{ pageDescription }}
			</span>
    </div>

    <b-row v-if="!selectedFormula">
      <b-card
        v-for="(charge, index) in charges"
        :key="index"
        @click="selectFormula(charge.type, true)"
        class="debt-type mb-2 w-100 cursor-pointer mx-1"
      >
        <div class="d-flex align-items-center">
          <b-avatar
            class="mr-1"
            size="30"
            :variant="charge.avatarVariant"
          >
            <img
              :src="charge.avatar"
              alt=""
              width="25"
            >
          </b-avatar>
          <h5 class="mb-0">
            {{ charge.title }}
          </h5>
        </div>
      </b-card>
    </b-row>
    <b-row v-else>
      <b-card class="w-100 mx-1">
        <div>
          <span
            class="menu-item-icon mr-1 d-inline-flex justify-content-center align-items-center"
            :style="{ background: generateBgForBox('#EA5455') }"
          >
          <chart-icon
            :style="{ color: '#EA5455' }"
          />
        </span>
          <span>
          فرمول شارژ {{ chargeName }}
        </span>
        </div>
        <p class="mt-2" style="font-size: 16px;font-weight: 400;line-height: 22px;">
          شارژ ثابت<br>
          <b style="font-size: 120%">+</b> متراژ × شارژ هر متر<br>
          <b style="font-size: 120%">+</b> تعداد ساکنین × شارژ هر نفر
        </p>
      </b-card>
      <b-card class="w-100 mx-1">
        <div>
          <span
            class="menu-item-icon mr-1 d-inline-flex justify-content-center align-items-center"
            :style="{ background: generateBgForBox('#00CFE8') }"
          >
          <chart-icon
            :style="{ color: '#00CFE8' }"
          />
        </span>
          <span>ثبت جزئیات فرمول</span>
        </div>
        <b-form
          class="formula-form mt-2"
          @submit.prevent
        >
          <b-form-group>
            <label>مبلغ شارژ ثابت</label>
            <b-form-input
              v-model="charge"
              @input="separate('charge')"
              id="charge-amount"
              name="charge-amount"
              style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
            />
          </b-form-group>
          <b-form-group>
            <label>مبلغ شارژ هر متر</label>
            <b-form-input
              @input="separate('chargePerMeter')"
              @click="buildingStatus.empty_area_count !== 0 ? $bvModal.show('area-modal'): ''"
              :readonly="buildingStatus.empty_area_count !== 0 "
              v-model="chargePerMeter"
              id="charge-per-meter"
              name="charge-per-meter"
              style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
            />
          </b-form-group>
          <b-form-group>
            <label>مبلغ شارژ هر نفر</label>
            <b-form-input
              v-model="chargePerPerson"
              @input="separate('chargePerPerson')"
              id="charge-per-person"
              name="charge-per-person"
              style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
            />
          </b-form-group>
          <b-button @click="registerFormula" variant="primary" block>
            ثبت
          </b-button>
        </b-form>
      </b-card>
    </b-row>
    <b-modal
      id="area-modal"
      size="sm"
      hide-footer
      hide-header
      hide-header-close
      centered
      :no-close-on-backdrop="true"
      :title="''"
    >
      <div class=" py-2">
        <div class="text-center">
          <p>برای ثبت مبلغ شارژ هر متر ابتدا باید متراژ {{buildingStatus.empty_area_count}} باقیمانده را ثبت کنید.</p>
        </div>
        <div class="text-center">
          <b-button :to="`/meterage?type=${selectedFormula}&redirect=${redirect}`" variant="primary" style="width: 40%" class="mr-1">ثبت متراژ</b-button>
          <b-button @click="$bvModal.hide('area-modal')" variant="outline-secondary" style="width: 40%">انصراف
          </b-button>
        </div>
      </div>
    </b-modal>
  </div>
</template>

<script>

import { generateBgForBox } from '@core/utils'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import AppCollapse from '@core/components/app-collapse/AppCollapse.vue'
import AppCollapseItem from '@core/components/app-collapse/AppCollapseItem.vue'
import {
    BAvatar,
    BButton,
    BCard,
    BCardBody,
    BCardTitle,
    BCol,
    BForm,
    BFormGroup,
    BFormInput,
    BImg,
    BInputGroup,
    BModal,
    BRow,
} from 'bootstrap-vue'
import axios from "@/libs/axios";
import ChartIcon from "@core/components/svg-icons/ChartIcon.vue";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import BookIcon from "@core/components/svg-icons/BookIcon.vue";
import { separator } from "@core/helper";

export default {
    name: "formula",
    components: {
        BookIcon,
        BAvatar,
        ChartIcon,
        BFormGroup,
        BFormInput,
        BInputGroup,
        BForm,
        AppCollapse,
        AppCollapseItem,
        BRow,
        BCol,
        BCard,
        BCardBody,
        BCardTitle,
        BImg,
        BButton,
        BModal,
        ValidationProvider,
        ValidationObserver,
    },
    data() {
        return {
            pageDescription: 'در این بخش می‌توانید فرمول محاسبه شارژ جاری یا عمرانی را تعریف کنید.',
            token: '',
            buildingStatus: {},
            formula: {},
            charge: '',
            chargeName: 'جاری / عمرانی',
            chargePerMeter: '',
            chargePerPerson: '',
            charges: [
                {
                    avatarVariant: 'light-primary',
                    avatar: require('@/assets/icons/bill-icon.svg'),
                    title: 'شارژ جاری',
                    type: 'current'
                },
                {
                    avatarVariant: 'light-warning',
                    avatar: require('@/assets/icons/other-orange.svg'),
                    title: 'شارژ عمرانی',
                    type: 'civil'
                },
            ],
            selectedFormula: '',
            redirect: ''
        }
    },
    mounted() {
        this.selectedFormula = this.$route.hash ? this.$route.hash : this.$route.query.type
        this.redirect = this.$route.query.redirect ? this.$route.query.redirect : ''
        if (this.selectedFormula) {
            this.chargeName = this.selectedFormula === 'civil' ? 'عمرانی' : 'جاری'
        }
        this.token = localStorage.getItem('token')
    },
    watch: {
        selectedFormula(val) {
            this.selectFormula(val)
        }
    },
    methods: {
        separate(value) {
            if (value === 'charge') {
                this.charge = separator(this.charge)
            }
            if (value === 'chargePerMeter') {
                this.chargePerMeter = separator(this.chargePerMeter)
            }
            if (value === 'chargePerPerson') {
                this.chargePerPerson = separator(this.chargePerPerson)
            }
        },
        generateBgForBox,
        async selectFormula(chargeType, redirect) {
            if (redirect) {
                this.$router.push('/formula?type=' + chargeType)
            }

            if (chargeType) {
                this.selectedFormula = chargeType
                this.chargeName = this.selectedFormula === 'civil' ? 'عمرانی' : 'جاری'
                if (chargeType === 'civil') {
                    this.pageDescription = 'شارژ عمرانی نوعی بدهی است که به مالک تعلق می‌گیرد. پرداخت این رقم سالیانه بوده و معمولا طبق یک فرمول هر سال توسط هیئت مدیره به تصویب می‌رسد. در این بخش با استفاده از سه گزینه عدد ثابت و متراژ واحد و تعداد ساکنین  می‌توانید فرمول محاسبه شارژ عمرانی ساختمان خود تعریف کنید.'
                } else {
                    this.pageDescription = 'شارژ جاری نوعی بدهی است که به ساکنین تعلق می‌گیرد. پرداخت این رقم ماهیانه بوده و معمولا طبق یک فرمول هر سال توسط هیئت مدیره به تصویب می‌رسد. در این بخش با استفاده از سه گزینه عدد ثابت و متراژ واحد و تعداد ساکنین می‌توانید فرمول شارژ جاری ساختمان خود تعریف کنید.'
                }
            }
            const { data } = await axios.get("manager/debt/formula/status", {
                params: {
                    type: chargeType,
                },
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });
            this.buildingStatus =  data.building_status
            this.formula = data.formula
            this.charge = separator(data.formula.fix)
            this.chargePerMeter = separator(data.formula.area)
            this.chargePerPerson = separator(data.formula.residents)
            // axios
            //     .get('//sm.oop.ir/api/v1/manager/formula/get', {
            //       params: {type: chargeType},
            //       headers: {
            //         'Authorization': 'Bearer ' + this.token
            //       }
            //     })
            //     .then(response => {
            //       // console.log(response)
            //       this.formula = response.data.list
            //       this.charge = response.data.list.fix
            //       this.chargePerMeter = response.data.list.area
            //       this.chargePerPerson = response.data.list.residents
            //     })
            //     .finally(() => {
            //         }
            //     )
        },
        registerFormula() {
            axios
                .post('//sm.oop.ir/api/v1/manager/formula/store',
                    {
                        type: this.selectedFormula,
                        fix: this.charge,
                        area: this.chargePerMeter,
                        residents: this.chargePerPerson
                    },
                    {
                        headers: {
                            'Authorization': 'Bearer ' + this.token
                        }
                    })
                .then(response => {
                    if (this.$route.query.redirect) {
                        this.$router.push({ path: `/debt-registration/list?type=${this.$route.query.redirect}`})
                    }
                    else {
                        this.$router.push({ path: '/'})

                    }
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: 'ثبت اطلاعات با موفقیت انجام شد',
                            icon: 'EditIcon',
                            variant: 'success',
                        },
                    })
                })
                .finally(() => {
                    }
                )
        }
    },
}
</script>

<style scoped>
.menu-item-icon {
    width: 36px;
    height: 36px;
    border-radius: 50%;
}
[dir] .form-control:disabled, [dir] .form-control[readonly] {
    background-color: #fff;
}
</style>
